.clearfix:before {
	content: ".";
	display: block;
	clear: both;
	height: 0;
	font-size: 0;
	visibility: hidden;
	overflow: hidden;
}
.clearfix:after {
	content: ".";
	display: block;
	clear: both;
	height: 0;
	font-size: 0;
	visibility: hidden;
	overflow: hidden;
}
.hidden {
	visibility: hidden;
}
.visible {
	visibility: visible;
}
.hide {
	display: none;
}
.show {
	display: block;
}
.container {
	width: 96%;
	max-width: 1200px;
	margin: 0 auto;
}
.container-fluid {
	width: 100%;
	margin: 0 auto;
}
.rowFluid {
	clear: both;
}
.rowFluid:before, .rowFluid:after, .column:before, .column:after {
	content: ".";
	display: block;
	clear: both;
	height: 0;
	font-size: 0;
	visibility: hidden;
	overflow: hidden;
}
/* 在线联系 */
#online {
	position: fixed;
	bottom: 126px !important;
	right: -40px;
	z-index: 1000;
	width: 32px;
	height: 32px;
	background: #dd9e09;
	color: #fff;
	cursor: pointer;
	border-radius: 3px;
	-webkit-transition: all 0.4s;
	transition: all 0.4s;
	opacity: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
#online i {
	display: block;
	width: 18px;
	height: 18px;
	margin: 3px 7px;
}
#online i img {
	max-width: 100%;
}
#online:hover {
	background: #333;
}
#online_lx {
	width: 180px;
	height: auto;
	border-radius: 5px;
	background-color: #fff;
	-webkit-box-shadow: 0 5px 9px rgba(4, 0, 0, .17);
	box-shadow: 0 5px 9px rgba(4, 0, 0, .17);
	position: fixed;
	right: 12px;
	bottom: 126px !important;
	z-index: 99999;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
#olx_head {
	padding: 20px;
	line-height: 16px;
	height: 16px;
	background: #00C0FF;
	border-radius: 5px 5px 0 0;
	color: #fff;
	font-size: 14px;
}
#online_close {
	display: block;
	float: right;
	width: 15px;
	height: 15px;
	cursor: pointer;
}
#online_close img {
	max-width: 100%;
	-webkit-transition: all 0.4s;
	transition: all 0.4s;
}
#online_close img:hover {
	transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
}
#olx_qq li a {
	padding: 12px 40px;
	height: 18px;
	line-height: 18px;
	background: #fff;
	color: #353434;
	display: block;
	border-bottom: #f5f5f5 1px solid;
	-webkit-transition: all ease-out .3s;
	transition: all ease-out .3s;
	color: #888;
}
#olx_qq li a:hover {
	background: #f5f5f5;
	text-indent: 10px;
}
#olx_qq li i {
	width: 20px;
	height: 20px;
	float: left;
	margin: 10px;
}
#olx_qq li i img {
	max-width: 100%;
}
#olx_tel {
	padding: 15px 10px;
}
#olx_tel div {
	color: #434343;
	line-height: 24px;
	height: 24px;
	margin: 0 25px 0 8px;
}
#olx_tel div i {
	width: 20px;
	height: 25px;
	float: left;
	margin-right: 2px;
}
#olx_tel div i img {
	max-width: 100%;
	max-height: 100%;
}
#olx_tel p {
	line-height: 30px;
	color: #888;
	text-align: center;
	padding-top: 10px;
}
