/* 灯箱 */
.lightWrap {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 6200;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, .6);
	overflow: hidden;
}
.lightWrap .closeLightBox {
	position: absolute;
	cursor: pointer;
	right: 10px;
	top: 10px;
	width: 50px;
	height: 50px;
	/* background: url(../images/icon_close.png)no-repeat center center; */
	background-size: 20px;
	-webkit-transition: all 0.4s;
	transition: all 0.4s;
}
.lightWrap .closeLightBox:hover {
	transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
}
.lightWrap .item {
	text-align: center;
	position: relative;
}
.lightWrap .item img {
	max-width: 100%;
	max-height: 100%;
}
.lightWrap .item .title {
	position: absolute;
	line-height: 1.6;
	bottom: 0;
	width: 100%;
	text-align: center;
	background: rgba(0, 0, 0, .4);
	color: #fff;
	padding: 10px 0;
}
.lightWrap .loading {
	/* background: url(../images/loader.gif) no-repeat center center; */
}
.lightWrap .owl-prev, .lightWrap .owl-next {
	position: absolute;
	width: 40px;
	height: 70px;
	text-align: center;
	top: 50%;
	margin-top: -35px;
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
}
.lightWrap .owl-prev {
	left: -60px;
	/* background: #555 url(../images/icon_prev2.png) no-repeat center center; */
}
.lightWrap .owl-next {
	right: -60px;
	/* background: #555 url(../images/icon_next2.png) no-repeat center center; */
}
.lightWrap .owl-prev:hover {
	opacity: .8;
	/* background: #00C0FF url(../images/icon_prev.png) no-repeat center center; */
}
.lightWrap .owl-next:hover {
	opacity: .8;
	/* background: #00C0FF url(../images/icon_next.png) no-repeat center center; */
}
.lightWrap:hover .owl-buttons .owl-prev {
	left: 10px;
}
.lightWrap:hover .owl-buttons .owl-next {
	right: 10px;
}
